function mapDispatchToProps(dispatch) {
  return {
    addTask: (task) => dispatch(addTask(task)),
  };
}

const CreateTaskContainer = () => {
  const [status] = useStatus();
  const [task, setTask] = useState({ name: "", status });
  const [invalidLength, setInvalidLength] = useState(false);
  //const [hasChanged, setHasChanged] = useState(false);

  const handleChange = (event) => {
    const value = event.target.value;
    setInvalidLength(value.length < 1 || value.length > 10);
    setTask(value);
    if (!invalidLength) setTask(value);
  };

  const handleSubmit = (event) => {
    addTask(task);
  };

  return <CreateTask onChange={handleChange} onSubmit={handleSubmit} />;
};

export default connect(null, mapDispatchToProps)(CreateTaskContainer);

