import { ADD_TASK, DELETE_TASK, EDIT_TASK } from '../constants'

const initialState = {
    tasks: []
}

export default function tasksReducer(state = initialState, action) {
    switch(action.type) {
        case ADD_TASK:
            console.log('se agrego la tarea al store')
            console.log(state)
            return { ...state, tasks: [action.data, ...state.tasks] }
        case DELETE_TASK:
            return { ...state, tasks: state.tasks.filter((el, index) => index !== action.data) }
        case EDIT_TASK:
            return { ...state, tasks: state.tasks.map((el, index) => index === action.data.index ? action.data.task : el) }
        default:
            return state
    }
} 

